
* {
  font-family: 'Raleway', sans-serif!important;
  text-align: center;
  box-sizing: border-box;
  
}

html,
body,
#root {
  height: 100%;
}

.App {
  display: flex;
  flex-direction: column;
  align-self: center;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.github-link {
  margin-top: 40px;
  font-size: 15px;
  color: #2980b9;
}

h1 {
  color: #545454;
}

h3 {
  margin: 0;
  color: #545454;
  font-size: 22px;
}

h4 {
  margin-bottom: 0;
  color: #545454;
  font-size: 18px;
}

.close-btn {
  position: absolute;
  right: 5px;
  top: 5px;
  cursor: pointer;
}

.react-chained-modal-container{
  position: relative;
  height: auto!important;
  padding: 30px!important;
}

.popup-header {
  text-align: center;
}

.next-btn {
  display: flex;
  align-items: center;
  justify-content: space-between;
  align-self: center;
  color: #368df4;
  cursor: pointer;
  margin-right: 10px;
}

.prev-btn {
  display: flex;
  align-items: center;
  justify-content: space-between;
  align-self: center;
  color: #368df4;
  cursor: pointer;
  margin-right: 10px;
}

.text-field {
  padding: 10px;
  border: 1px solid #368df4;
  border-radius: 3px;
  display: flex;
  max-width: 21%
}

.description-text {
  margin: 25px 0 40px
}

.app-btn{
  margin-top: 0 !important;
}

.next-btn-holder {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-top: 20px;
}

.next-btn-text {
  padding:0 10px
}

.raised-button {
  padding: 10px 15px;
  border-radius: 3px;
  cursor: pointer;
  letter-spacing: 0.8px;
  white-space: nowrap;
  font-size: 14px;
  font-weight: bold;
}

.btn-red {
  color: #CD0101;
  border: 1px solid #CD0101;
}

.btn-blue {
  color: #368df4;
  border: 1px solid #368df4;
}

.btn-green {
  color:  #2ecc71;
  border: 1px solid #2ecc71;
}

.popup-description {
  text-align: center;
  margin: 40px;
  letter-spacing: 0.8px;
}

.plans-holder {
  display: flex;
  width: 100%;
  justify-content: space-around;
}

.plan-description {
  line-height: 1.6;
  max-width: 31%;
  display: flex;
  flex-direction: column;
}

.button-plan {
  margin-top: auto;
}

.steps {
  font-size: 14px;
  margin-top: 10px;
}

.rccs__demo__content {
  display: flex;
  margin-top: 40px;
}

input[type="text"],
input[type="tel"] {
    padding: 10px 15px;
    border: 1px solid #368df4;
    border-radius: 3px;
    margin-bottom: 15px;
}